.hero-section {
    height: 100vh;
    background-size: cover;
    background-position: center;
    position: relative;
    transition: background-image 1s ease-in-out;
    overflow: hidden;
}

.hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.9) 40%, rgba(255, 255, 255, 0));
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 8%;
}

.hero-text {
    max-width: 600px;
    z-index: 2;
}

.hero-text h1 {
    font-size: 3rem;
    font-weight: 700;
    color: #002147;
    margin-bottom: 20px;
}

.hero-text p {
    font-size: 1.2rem;
    color: #444;
    line-height: 1.6;
}

/* Fade-in effect */
.fade-in {
    animation: fadeIn 1s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateX(50px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

/* Controls: arrows + dots */
.hero-controls {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 15px;
    z-index: 10;
}

.hero-controls .arrow {
    font-size: 1.8rem;
    color: #004aad;
    /* background-color: rgba (255, 255, 255, 0.6); */
    border-radius: 50%;
    padding: 8px 12px;
    cursor: pointer;
    transition: background 0.3s;
}

.hero-controls .arrow:hover {
    background-color: rgba(143, 143, 143, 0.9);
}

.hero-controls .dot {
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background: #bbb;
    display: inline-block;
    cursor: pointer;
    transition: background 0.3s;
}

.hero-controls .dot.active {
    background: #004aad;
}

/* Responsive */
@media (max-width: 768px) {
    .hero-text h1 {
        font-size: 2rem;
    }

    .hero-text p {
        font-size: 1rem;
    }

    .hero-controls {
        bottom: 20px;
        gap: 10px;
    }

    .hero-controls .arrow {
        font-size: 1.5rem;
        padding: 6px 10px;
    }
}